import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../http.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Clients} from '../../clients';
import {Observable} from 'rxjs';

@Component({
  selector: 'app-list-of-clients',
  templateUrl: './list-of-clients.component.html',
  styleUrls: ['./list-of-clients.component.scss'],
  providers: [HttpService]
})
export class ListOfClientsComponent implements OnInit {
  allInfo: any[] = [];
  clients: Observable<Clients>;
  selectedUser: Clients;
  searchSrt: any = '';
  constructor(private httpService: HttpService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.getClient();
  }
  getClient(): void {
    this.httpService.getData().subscribe(users => {
      this.allInfo = users.map( u => {
        return {
          firstName: u.general.firstName,
          lastName: u.general.lastName,
          avatar: u.general.avatar,
          company: u.job.company,
          title: u.job.title,
          email: u.contact.email,
          phone: u.contact.phone,
          street: u.address.street,
          city: u.address.city,
          zipCode: u.address.zipCode,
          country: u.address.country
        };
      });
    });
  }
  next(userInfo: Clients) {
    this.router.navigate(['/users', userInfo.firstName]);
  }
  onSelect(user: Clients): void {
    this.selectedUser = user;
  }
}
